body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p,h1,h2,h3,h4,h5,ul,li {
  padding: 0;
  margin: 0;
}

.game {
  min-width: 1140px;
  max-width: 1920px;
  margin: 0 auto;
  padding: 15px;
  display: flex;
  justify-content: center;
}

.game__left-block {
  width: 810px;
  margin-right: 20px;
}

.game__toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.game__toolbar-minTitle {
  margin-bottom: 5px;
}

.game__toolbar-block {
  font-size: 30px;
  line-height: 1;
  padding: 3px 20px;
  border: 2px solid #000;
}

.game__result {
  height: 100%;
  width: 250px;
  border: 2px solid #000;
  padding: 10px;
}

.game__result-title {
  text-align: center;
  font-size: 25px;
  font-weight: 700;
  margin-bottom: 20px;
}

.game__result-user {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.game__result-user-name {
  font-size: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 5px;
}

.game__result-user-score {
  font-size: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.game__overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  height: 100%;
  width: 100%;
  font-size: 30px;
  font-weight: 500;
  color: #fff;
  background-color: rgba(32, 64, 195, .7);
  transition: background-color 0.8s linear;
}